import React, { useState, useEffect, useContext } from "react";
import "semantic-ui-css/semantic.min.css";
import "./App.css";
import bocadillo from "./images/bocadillo.png";
import chocolate from "./images/chocolate.png";
import coca from "./images/coca.png";
import cena from "./images/cena.png";
import imageCompression from "browser-image-compression";
import { storage } from "./firebase/firebase";

import { Carrito } from "./Contexto";
import {
  Container,
  Segment,
  Header,
  Statistic,
  Table,
  List,
  Popup,
  Image,
  Label,
  Button,
  Icon,
  Modal,
  Dimmer,
  Loader,
  Input,
  Divider,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";

function currencyFormat(num) {
  return "$" + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}

function makeid(length) {
  var result = "";
  var characters =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}

function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const fechaA = a.Disponibles;
  const fechaB = b.Disponibles;

  let comparison = 0;
  if (fechaA > fechaB) {
    comparison = -1;
  } else if (fechaA < fechaB) {
    comparison = 1;
  }
  return comparison;
}

export default function Productos() {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState();
  const [buscar, setBuscar] = useState("");
  const [categoria, setCat] = useState("Snacks");
  const dummyImg = "https://react.semantic-ui.com/images/wireframe/image.png";
  const [previewImg, setPreviewImg] = useState(dummyImg);
  const [activeItem, setActiveItem] = useState("");
  const allInputs = { imgUrl: "" };
  const [imageAsFile, setImageAsFile] = useState("");
  const [imageAsUrl, setImageAsUrl] = useState(allInputs);
  const [subir, setSubir] = useState(false);
  const [saving, setSaving] = useState(false);
  const [nombre, setNombre] = useState();
  const [cant, setCant] = useState();
  const [precio, setPrecio] = useState();
  const [open, setOpen] = useState(false);
  const categorias = [
    { name: "Snacks", src: bocadillo, categ: "Snacks" },
    { name: "Dulces", src: chocolate, categ: "Dulces" },
    { name: "Bebidas", src: coca, categ: "Bebida" },
    { name: "Otros", src: cena, categ: "Otros" },
  ];
  const sacardata = async () => {
    setLoading(true);
    var data = [];
    let datas = [];
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[711905343];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;
    let lastr = rows.length + 1;
    var j, i;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setData(datas);
    console.log(datas);
    setLoading(false);
  };

  async function handleImageUpload(event, id) {
    console.log(id);
    if (event.target.files && event.target.files[0]) {
      const imageFile = event.target.files[0];
      var reader = new FileReader();

      var preview = document.getElementById(id);
      reader.onload = function (e) {
        preview.setAttribute("src", e.target.result);
      };

      reader.readAsDataURL(event.target.files[0]);
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true,
      };
      try {
        const compressedFile = await imageCompression(imageFile, options);
        setImageAsFile((imageFile) => compressedFile);
      } catch (error) {
        console.log(error);
      }
      setSubir(true);
    } else {
      setSubir(false);
      setImageAsFile("");
      setImageAsUrl("");
      preview = document.querySelector(id);
      preview.setAttribute("src", "");
      var archivoSelect = document.querySelector("#anexoImg");
      archivoSelect.value = "";
    }
  }

  const handleFireBaseUpload = (e) => {
    if (subir) {
      if (imageAsFile === "") {
        console.error(
          `not an image, the image file is a ${typeof imageAsFile}`
        );
      }
      const uploadTask = storage
        .ref(`/images/${imageAsFile.name}`)
        .put(imageAsFile);
      uploadTask.on(
        "state_changed",
        (snapShot) => {
          //takes a snap shot of the process as it is happening
          console.log(snapShot);
        },
        (err) => {
          //catches the errors
          console.log(err);
        },
        () => {
          // gets the functions from storage refences the image storage in firebase by the children
          // gets the download url then sets the image from firebase as the value for the imgUrl key:
          storage
            .ref("images")
            .child(imageAsFile.name)
            .getDownloadURL()
            .then((fireBaseUrl) => {
              setImageAsUrl((prevObject) => ({
                ...prevObject,
                imgUrl: fireBaseUrl,
              }));
              console.log(fireBaseUrl);
            });
        }
      );
      setSubir(false);
    }
  };

  const registrar = async () => {
    setSaving(true);
    console.log(activeItem);
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[711905343];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    let lastr = rows.length + 1;
    var j;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      if (sheet.getCell(j, 0).value === activeItem.id) {
        const precio = sheet.getCell(j, 2);
        const imagen = sheet.getCell(j, 3);
        const inventario = sheet.getCell(j, 5);
        if (imageAsUrl.imgUrl) {
          imagen.value = imageAsUrl.imgUrl;
        }
        precio.value = parseFloat(activeItem.price);
        inventario.value = parseFloat(activeItem.disponible);
        await sheet.saveUpdatedCells();
        break;
      }
    }
    setImageAsUrl(allInputs);
    setData();
    setSaving(false);
  };

  const registrarNuevo = async () => {
    setSaving(true);
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[711905343];
    var i;
    var pID;
    var checkID = 1;
    do {
      pID = makeid(8);
      checkID = data.filter((item) => {
        return item.Product_Id === pID;
      }).length;
    } while (checkID != 0);

    const newData = await sheet.addRow({
      Product_Id: pID,
      Product_Name: nombre,
      Price: precio,
      Disponibles: cant,
      Image: imageAsUrl.imgUrl,
      Categoria: categoria,
    });
    setImageAsUrl(allInputs);
    setData();
    setSaving(false);
  };

  useEffect(() => {
    !data && sacardata();
  }, [data]);

  useEffect(() => {
    subir && handleFireBaseUpload();
  }, [subir]);

  return (
    <Container>
      <Dimmer active={loading || saving} page>
        <Loader>{saving ? "Saving" : "Loading"}</Loader>
      </Dimmer>
      <Modal open={open}>
        <Modal.Header>Nuevo Producto</Modal.Header>
        <Modal.Content image>
          <Image size="medium" id="nuevoProd" src={previewImg} wrapped />
          <Modal.Description>
            <p>NOMBRE</p>
            <Input
              type="text"
              onChange={(e) => {
                setNombre(e.target.value);
              }}
            ></Input>
            <p>DISPONIBLES</p>
            <Input
              type="number"
              onChange={(e) => {
                setCant(e.target.value);
              }}
            ></Input>
            <p>PRECIO</p>
            <Input
              labelPosition="right"
              type="number"
              placeholder="Amount"
              onChange={(e) => {
                setPrecio(e.target.value);
              }}
            >
              <Label basic>$</Label>
              <input
                type="number"
                onChange={(e) => {
                  setPrecio(e.target.value);
                }}
              />
            </Input>
            <Input
              id="anexoImg"
              fluid
              className="input-image"
              type="file"
              onChange={(e) => {
                handleImageUpload(e, "nuevoProd");
              }}
            />
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button color="red" onClick={() => setOpen(false)}>
            Cancelar
          </Button>
          <Button
            content="Guardar"
            labelPosition="right"
            icon="checkmark"
            onClick={() => {
              setOpen(false);
              registrarNuevo();
            }}
            positive
          />
        </Modal.Actions>
      </Modal>

      <Segment style={{ display: "flex", height: "10vh" }}>
        {categorias.map((cat) => (
          <p style={{ margin: "auto" }}>
            <Popup
              content={cat.name}
              key={cat.name}
              trigger={
                <Image
                  src={cat.src}
                  size="mini"
                  onClick={() => {
                    setCat(cat.name);
                  }}
                />
              }
            />
            <span style={{ color: cat.name === categoria ? "red" : "black" }}>
              {cat.name}
            </span>
          </p>
        ))}
      </Segment>
      <div style={{ display: "flex" }}>
        <Input
          icon="search"
          placeholder="Buscar..."
          size="small"
          onChange={(e, { value }) => {
            setBuscar(value);
          }}
        />
        <Button
          icon="add"
          content="Nuevo"
          color="green"
          onClick={() => {
            setOpen(true);
          }}
        />
      </div>
      <Segment style={{ height: "50vh", overflowY: "scroll" }}>
        {data &&
          data
            .sort(compare)
            .filter((item) => {
              return (
                item.Product_Name.toLowerCase().includes(
                  buscar ? buscar.toLowerCase() : buscar
                ) && item.Categoria === categoria
              );
            })
            .map((item) => (
              <div
                style={{
                  width: "50vw",
                  margin: "auto",
                  display: "block",
                }}
              >
                <p style={{ display: "inline-block", width: "50vw" }}>
                  {item.Product_Name}
                </p>
                <img
                  id={item.Product_Id}
                  src={item.Image}
                  alt=""
                  width="120"
                  height="120"
                ></img>
                <div style={{ display: "inline-block", width: "50vw" }}>
                  <Label>$</Label>
                  <Input
                    type="number"
                    step="0.05"
                    style={{ width: "27vw" }}
                    defaultValue={item.Price}
                    onChange={(e, { value }) => {
                      setPrecio(value);
                      setActiveItem((prevObject) => ({
                        ...prevObject,
                        name: item.Product_Name,
                        price: value,
                        src: item.Image,
                        id: item.Product_Id,
                        disponible: cant ? cant : item.Disponibles,
                      }));
                    }}
                  ></Input>
                </div>
                <div style={{ display: "inline-block", width: "50vw" }}>
                  <Label>U</Label>
                  <Input
                    type="number"
                    step="1"
                    style={{ width: "27vw" }}
                    defaultValue={item.Disponibles}
                    onChange={(e, { value }) => {
                      setCant(value);
                      setActiveItem((prevObject) => ({
                        ...prevObject,
                        name: item.Product_Name,
                        price: precio ? precio : item.Price,
                        src: item.Image,
                        id: item.Product_Id,
                        disponible: value,
                      }));
                    }}
                  ></Input>
                </div>
                <Input
                  id="anexoImg"
                  fluid
                  className="input-image"
                  type="file"
                  onChange={(e) => {
                    handleImageUpload(e, item.Product_Id);
                    setActiveItem((prevObject) => ({
                      ...prevObject,
                      name: item.Product_Name,
                      price: precio ? precio : item.Price,
                      src: item.Image,
                      id: item.Product_Id,
                      disponible: cant ? cant : item.Disponibles,
                    }));
                  }}
                />
                <br />
                <Button
                  color="green"
                  onClick={() => {
                    registrar();
                  }}
                >
                  SAVE
                </Button>
                <Divider />
              </div>
            ))}
      </Segment>
    </Container>
  );
}
